/*Formularios(Registro, login, contacto)*/
.form-container {
  width: 70vw;
  min-width: 800px;
  height: 600px;
  display: flex;
  justify-content: center;
  background-color: #fff;
  align-items: center;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  border-radius: 10px;
  margin: 15em auto;
}
@media (max-width: 800px) {
  .form-container {
    flex-direction: column;
    height: initial;
    min-width: initial;
    box-shadow: none;
    width: 90vw;
    margin: 7em auto;
  }
}
.form-container img {
  width: 50%;
  height: 100%;
  object-fit: cover;
  object-position: left;
  border-radius: 10px 0 0 10px;
}
@media (max-width: 800px) {
  .form-container img {
    border-radius: 10px;
    width: 100%;
    height: 50%;
  }
}
.form-container form {
  padding: 20px;
  width: 50%;
  background-color: #fff;
  display: flex;
  flex-direction: column;
}
@media (max-width: 800px) {
  .form-container form {
    width: 100%;
    height: 50%;
    justify-content: center;
  }
}
.form-container form h1 {
  width: 100%;
  text-align: center;
}
.form-container form a {
  text-align: center;
}
.form-container form input,
.form-container form textarea {
  display: block;
  margin: 10px auto;
}

/*Boton formularios*/
.submit-button {
  margin: 10px auto;
  display: block;
  padding: 1em;
  background-color: #bccddb;
  border: 15px;
  border-radius: 6px;
  font-weight: bold;
  font-size: large;
  cursor: pointer;
}
.submit-button:hover {
  background: #9ba6af;
  transition: 0.4s;
}

/*Componente input*/
.input-wrapper {
  position: relative;
  text-align: center;
  margin-bottom: 10px;
}
.input-wrapper .input-wrapper__input {
  display: block;
  font-family: inherit;
}
.input-wrapper .input-wrapper__input:focus {
  outline: none;
}
.input-wrapper .input-wrapper__msg-error {
  position: absolute;
  bottom: -12px;
  left: 0;
  right: 0;
  color: red;
}

/*Componente ShoppingCart*/
.shopping-cart-mobile {
  position: fixed;
